import React from 'react';
import PropTypes from 'prop-types';
import {Card, Elevation, Intent} from "@blueprintjs/core";
import "./index.css";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {onMachinesUpdated, initListeners} from "../../actions/socket/MachinesActions";
import {loadMachines} from "../../actions/MachinesActions";
import {loadCenters} from "../../actions/CentersActions";
import {UberMap} from './Map/index';
import queryString from 'query-string';
import {withRouter} from 'react-router-dom';
import _isEqual from 'lodash/isEqual';
import _find from 'lodash/find';
import Toaster from "../Common/Toaster";


class MachinesMap extends React.Component {

    state = {
        current: null, 
        machine: null, 
        center: null,
        q: "",
        machine: null
    };

    componentDidMount(){
        const {authUser: {token}, userChannel, location: {search}, onMachinesUpdated, loadMachines, loadCenters} = this.props;
        initListeners({channel: userChannel, onMachinesUpdated});
        loadMachines(token, {});
        loadCenters(token, {map: true});
        // let params = queryString.parse(search);
        // params.q && this.setState({q: params.q});
    }

    static getDerivedStateFromProps(nextProps, prevState){
        const {location: {search: nextSearch}, machines, centers} = nextProps;
        const {q: prevQuery, machine: prevMachine} = prevState;

        const {q} = queryString.parse(nextSearch);

        console.log('prevState', prevQuery, q);

        const machine = _find(machines, {name: q}) || null;

        if(_isEqual(machine, prevMachine))
            return null;

        return {machine};
    }

    componentDidUpdate(prevProps, prevState){
        const {machines} = this.props;
        const {searchQuery: nextQ} = this.state;
        const {searchQuery: prevQ} = prevState;

        // if(!_isEqual(nextQ, prevQ)){
        //     const machine = _find(machines, {name: nextQ});
        //     if(machine){
        //         this.setState({machine});
        //     }else{
        //         Toaster.show({
        //             message: `No machine with ${nextQ} as a name ðŸ˜ž`,
        //             intent: Intent.DANGER,
        //             icon: 'error'
        //         });
        //     }
        // }
    }

    _performSearch = () => {
        const {q} = this.state;
        if(q.trim()){
            this.props.history.push(`/map?q=${q}`);
        }
    }

    render() {
        const {centers} = this.props;

        return (
            <div className="Map__Page">
                <div className="Map__Box">
                    <UberMap centers={centers} query={this.state.machine}/>
                </div>

                <Card
                    elevation={Elevation.TWO}
                    className="Machines__Box">
                    <div className="Header">
                        <div className="bp3-input-group .modifier">
                            <span className="bp3-icon bp3-icon-search"/>
                            <input 
                                onChange={({target: {value}}) => this.setState({q: value})}
                                value={this.state.q}
                                type="text" className="bp3-input" placeholder="Machine name..."/>
                            <button 
                                onClick={() => this._performSearch()}
                                className="bp3-button bp3-minimal bp3-intent-primary bp3-icon-arrow-right"/>
                        </div>
                    </div>
                </Card>

            </div>
        );
    }

    static propTypes = {
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired,
        userChannel: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        onMachinesUpdated: PropTypes.func.isRequired,
        loadMachines: PropTypes.func.isRequired,
        loadCenters: PropTypes.func.isRequired,
        machinesLocations: PropTypes.array.isRequired,
        centers: PropTypes.array.isRequired,
    }
}

const mapStateToProps = ({authUser, userChannel, machinesLocations, mapCenters, machines}) => (
    {authUser, userChannel, machinesLocations, centers: mapCenters, machines});
const mapDispatchToProps = dispatch => bindActionCreators({onMachinesUpdated, loadMachines, loadCenters}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MachinesMap));


